.program-rule {
	padding-top: 74px;
	padding-bottom: 110px;
	background: url(../assets/img/bg-programerule.png) no-repeat;
	background-size: 100% 100%;
	@media (max-width: 1024px) {
		padding: 20px 0;
	}
	.main--title--img {
		margin-bottom: 85px;
		@media (max-width: 1024px) {
			margin-bottom: 20px;
		}
	}
	.programe-rule__wrapper {
		position: relative;
	}
	.programe-rule--inner {
		position: relative;
	}
	.programe-rule__left__wrapper {
		max-width: 50%;
		padding-right: 60px;
		@media (max-width: 1439.98px) {
			padding-right: 10px;
		}
		@media (max-width: 1024.98px) {
			max-width: 100%;
			display: flex;
			flex-wrap: wrap;
			margin-bottom: 20px;
			> div {
				-ms-flex: 0 0 50%;
				flex: 0 0 50%;
				max-width: 50%;
			}
			.rule--view {
				align-self: center;
				text-align: center;
			}
			.wrapper {
				max-width: 50%;
			}
		}
		@media (max-width: 767.98px) {
			.wrapper {
				max-width: 100%;
			}
			> div {
				-ms-flex: 0 0 100%;
				flex: 0 0 100%;
				max-width: 100%;
			}
			.rule--view {
				margin-bottom: 10px;
			}
		}
		.title {
			margin-bottom: 20px;
			h3 {
				font-size: 2.5rem;
				font-family: Lato-Bold;
				color: map-get($color, main-4);
			}
			@media (max-width: 1439.98px) {
				margin-bottom: 0;
			}
		}
		.sub-title {
			padding-left: 15px;
			margin-bottom: 35px;
			@media (max-width: 1439.98px) {
				margin-bottom: 0;
			}
			li {
				color: map-get($color, main-8);
				font-size: 1.8rem;
				font-family: Lato-Regular;
				margin-bottom: 10px;
				list-style-image: url(../assets/img/maker.png);
			}

			ul {
				padding: 0;
				list-style: none;
				li:first-child {
					margin-top: 10px;
				}
				li {
					list-style: none;
				}
			}
		}
		.rule--view {
			.playgame__wrapper {
				border-radius: 30px;
				&::after {
					content: none;
				}
			}
			.btn-playgame {
				font-family: Lato-Bold;
				font-size: 2rem;
				width: 233px;
				height: 54px;
				border-radius: 30px;
				z-index: 0;
				&::after {
					content: none;
				}

				&::before {
					content: none;
					// z-index: 0;
					// background: linear-gradient(90deg, rgba(213,184,95,1) 0%, rgba(34,100,57,1) 100%);
					// width: 100%;
					// height: 100%;
					// background-size: 300% 300%;
					// animation: AnimationBackGroundGradient 5s ease infinite;
				}
			}
		}
	}
	.block-out-container {
		max-height: 771px;
		position: absolute;
		right: 0;
		top: 50%;
		transform: translateY(-50%);
		max-width: 50%;
		@media (max-width: 1024.98px) {
			position: static;
			right: unset;
			top: unset;
			transform: unset;
			max-width: 100%;
		}
	}
}
.swiper-custom-common {
	max-height: 771px;
	overflow: hidden;
	.swiper-container {
		height: 100%;
	}
	.swiper-slide {
		max-height: 771px;
		height: auto;
	}
	.swiper-slide {
		.img {
			height: 100%;
			img {
				transform: scale(0.8);
				transition: 1s all ease-in-out;
			}
		}
	}
	.swiper-slide-active {
		.img {
			height: 100%;
			img {
				transform: scale(1);
				transition: 1s all ease-in-out;
			}
		}
	}
}
.summary {
	padding: 160px 0 110px 0;
	background: url(../assets/img/5.png) no-repeat;
	background-size: 100% 100%;
	position: relative;
	@media (max-width: 1024px) {
		padding: 20px 0;
	}
	&::after {
		position: absolute;
		content: "";
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background: map-get($color, main-6);
		z-index: 0;
		opacity: 87%;
		pointer-events: none;
	}
	.summary__wrapper {
		margin-bottom: 160px;
		z-index: 10;
		position: relative;
		display: flex;
		align-items: center;
		@media (max-width: 1024.98px) {
			flex-wrap: wrap;
			margin-bottom: 30px;
			> div {
				-ms-flex: 0 0 100%;
				flex: 0 0 100%;
				max-width: 100%;
			}
		}
		.summary__right {
			position: absolute;
			left: 0;
			top: 50%;
			transform: translateY(-50%);
			max-width: 50%;
			@media (max-width: 1024px) {
				position: static;
				top: unset;
				right: unset;
				max-width: 100%;
				transform: unset;
			}
		}
		.summary__left {
			width: 100%;
			align-self: center;
			.summary__left__wrapper {
				margin-left: auto;
				max-width: 50%;
				@media (max-width: 1024.98px) {
					max-width: 100%;
					margin-left: unset;
					margin-bottom: 10px;
				}
				.main-title {
					margin-bottom: 50px;
					@media (max-width: 1024.98px) {
						margin-bottom: 20px;
					}
				}
				.summary__left--inner {
					.wrapper {
						display: flex;
						flex-wrap: wrap;
						margin-right: -10px;
						.item {
							margin-bottom: 50px;
							padding-left: 60px;
							@media (max-width: 1024.98px) {
								padding-left: 10px;
								padding-right: 10px;
								margin-bottom: 10px;
							}
							p {
								color: map-get($color, main-8);
								font-size: 1.8rem;
								font-family: Lato-Regular;
								span {
									font-family: Lato-Bold;
									color: map-get($color, main-4);
								}
							}
							-ms-flex: 0 0 50%;
							flex: 0 0 50%;
							max-width: 50%;
							@media (max-width: 400.98px) {
								-ms-flex: 0 0 100%;
								flex: 0 0 100%;
								max-width: 100%;
							}
						}
						@media (max-width: 1024.98px) {
							margin-bottom: 20px;
						}
					}
				}
				.brochure-btn {
					margin-bottom: 20px;
				}
				.info-project {
					@media (max-width: 1024.98px) {
						margin-bottom: 20px;
					}
				}
				.custom-btn {
					.playgame__wrapper {
						border-radius: 30px;
						&::after {
							content: none;
						}
						.btn-playgame {
							font-family: Lato-Bold;
							width: 272px;
							font-size: 2rem;
							height: 54px;
							&::after {
								content: none;
							}
							&::before {
								content: none;
							}
						}
					}
				}
			}
		}
	}
}
.summary__wrapper--bottom {
	.swiper-container {
		@media (min-width: 1500px) {
			margin-left: -50px;
			margin-right: -50px;
		}
	}
	.swiper-slide {
		@media (min-width: 1500px) {
			-ms-flex: 0 0 20%;
			flex: 0 0 20%;
			max-width: 20%;
			padding: 0 50px;
		}
	}
	.item {
		margin: 0 auto;
		max-width: 180px;
		text-align: center;
		.img {
			margin-bottom: 25px;
			svg {
				transition: 0.5s all ease-in-out;
			}
		}
		@media (max-width: 1024.98px) {
			margin: 0 auto;
		}
		@media (min-width: 1025px) {
			&:hover {
				svg {
					transform: rotateY(180deg);
					transition: 0.5s all ease-in-out;
				}
			}
		}
	}
	p {
		font-size: 1.8rem;
		color: map-get($color, main-8);
		@media (max-width: 575.98px) {
			font-size: 1.6rem;
		}
	}
}
.customer-list {
	position: relative;
	padding: 100px 0;
	background: url(../assets/img/bg-programerule.png) no-repeat;
	background-size: 100% 100%;
	@media (max-width: 1024px) {
		padding: 20px 0;
	}
}
.customer-list__wrapper {
	display: flex;
	justify-content: center;
	.customer-list__wrapper--inner {
		max-width: 750px;
		.main-title-img {
			margin-bottom: 80px;
			@media (max-width: 1024px) {
				margin-bottom: 20px;
			}
		}
		.wrapper {
			transition: 0.3s all ease-in-out;
		}
		.player--table {
			width: 100%;
			border: 2px solid map-get($color, main-4);
			padding: 30px 70px;
			box-shadow: 0 3px 3px rgba($color: #000000, $alpha: 0.7);
			border-radius: 20px;
			background: rgba($color: #000000, $alpha: 0.16);
			margin-bottom: 65px;
			@media (max-width: 575.98px) {
				padding: 5px;
				margin-bottom: 20px;
			}
			.column-3 {
				@media (min-width: 1025px) {
					text-align: unset !important;
					padding-left: 50px;
				}
			}
			.table--title {
				display: flex;
				padding-bottom: 30px;
				border-bottom: 1px solid rgba($color: #ffffff, $alpha: 0.2);
				margin-bottom: 15px;
				.column-1 {
					padding-right: 50px;
				}
				> div {
					-ms-flex: 0 0 33.33%;
					flex: 0 0 33.33%;
					max-width: 33.33%;
					text-align: center;
					p {
						font-family: Lato-Bold;
						font-size: 2rem;
						color: map-get($color, main-4);
					}
				}
			}
			.list-player {
				display: flex;
				flex-wrap: wrap;
				border-bottom: 1px;
				.column-1 {
					padding-right: 50px;
				}
				> div {
					-ms-flex: 0 0 33.33%;
					flex: 0 0 33.33%;
					max-width: 33.33%;
					word-break: break-all;
					text-align: center;
					margin-bottom: 15px;
					p {
						font-size: 1.8rem;
						color: map-get($color, main-8);
					}
				}
			}
		}
	}
}

.pagination {
	width: 100%;
	display: flex;
	justify-content: center;
	list-style-type: none;
	padding-left: 0;
	li {
		margin-right: 1.5rem;
		a {
			display: flex;
			justify-content: center;
			align-items: center;
			width: 35px;
			height: 35px;
			font-family: Lato-Regular;
			font-size: 25px;
			text-decoration: none;
			transition: all 0.2s cubic-bezier(0.2, 0.5, 0.5, 0.8);
			color: map-get($color, main-8);
		}
		&:hover {
			a {
				transition: all 0.2s cubic-bezier(0.2, 0.5, 0.5, 0.8);
				font-family: Lato-Bold;
				color: map-get($color, main-4);
			}
		}
		&.active {
			a {
				font-family: Lato-Bold;
				pointer-events: none;
				text-decoration: none;
				color: map-get($color, main-4);
			}
		}
		&.pagination-prev,
		&.pagination-next {
			background: rgba($color: #dcc375, $alpha: 0.77);
			position: relative;
			&::before {
				content: "";
				position: absolute;
				pointer-events: none;
				top: 50%;
				left: 50%;
				transform: translate(-50%, -50%);
				width: 6px;
				height: 11px;
				background-size: 100% 100%;
			}
			&.disable {
				pointer-events: none;
				background: rgba($color: #dcc375, $alpha: 0.48);
			}
		}
		&.pagination-prev {
			border-radius: 7px 0 0 7px;
			&::before {
				background: url(../assets/img/prev.png) no-repeat;
			}
		}
		&.pagination-next {
			border-radius: 0px 7px 7px 0px;
			&::before {
				background: url(../assets/img/next.png) no-repeat;
			}
		}
		@media (max-width: 575.98px) {
			margin: 0;
		}
	}
	@media (max-width: 1024.98px) {
		li {
			a {
				font-size: 14px;
			}
		}
	}
}

