#quiz-answer {
	width: 100%;
	background: url(../assets/img/bg-popup-rule.png) no-repeat;
	background-size: 100% 100%;
	position: relative;
	overflow: hidden;
	height: 100%;
	.container {
		height: 100%;
		overflow-y: auto;
	}
	@media (max-width: 1440.98px) {
		overflow-y: auto;
	}
	p,
	label,
	span {
		color: map-get($color, main-8);
		font-size: 2.4rem;
		@media (max-width: 1024.98px) {
			font-size: 1.8rem;
		}
	}
	.main-title-img {
		margin-bottom: 60px;
		@media (max-width: 1024.98px) {
			margin-bottom: 20px;
		}
	}
	.quiz-content {
		display: grid;
		grid-template-rows: min-content 25% min-content; // based on slidesPerColumn
		grid-auto-flow: column;
		grid-gap: 20px;
		@media (max-width: 1200.98px) {
			grid-template-rows: unset; // based on slidesPerColumn
			grid-template-columns: auto;
			grid-auto-flow: unset;
			grid-gap: 5px;
		}
	}
	.item {
		&:last-child {
			display: flex;
			justify-content: flex-end;
			align-items: flex-end;
			.playgame__wrapper {
				&::after {
					content: none;
				}
			}
			.btn-playgame {
				width: 156px;
				height: 60px;
				font-size: 2rem;
				font-family: Lato-Bold;
				&::after,
				&::before {
					content: none;
				}
				@media (max-width: 1024.98px) {
					width: 100px;
					height: 40px;
					font-size: 1.6rem;
				}
			}
		}
		input[type="radio"] {
			position: relative;
		}
	}
	.question {
		margin-bottom: 20px;
		display: flex;
		@media (max-width: 1024.98px) {
			margin-bottom: 5px;
		}
		.question-item {
			-ms-flex: 0 0 22%;
			flex: 0 0 22%;
			max-width: 22%;
			p {
				font-family: Lato-Bold;
				color: map-get($color, main-4);
			}
			@media (max-width: 1200.98px) {
				-ms-flex: 0 0 15%;
				flex: 0 0 15%;
				max-width: 15%;
			}
			@media (max-width: 525.98px) {
				-ms-flex: 0 0 30%;
				flex: 0 0 30%;
				max-width: 30%;
			}
		}
		.question-item-desc {
			-ms-flex: 0 0 78%;
			flex: 0 0 78%;
			max-width: 78%;
			@media (max-width: 525.98px) {
				-ms-flex: 0 0 70%;
				flex: 0 0 70%;
				max-width: 70%;
			}
			
		}
	}
	.answer--item {
		margin-bottom: 20px;
		padding-left: 22%;
		
		@media (max-width: 1024.98px) {
			margin-bottom: 5px;
			padding-left: 14%;
		}
		@media (max-width: 525.98px) {
			padding-left: 30%;
		}
		.wrapper {
			display: inline-flex;
			// align-items: center;
			// @media (max-width: 575.98px) {
			// 	align-items: center;
			// }
			transition:.1s all ease-in-out;
			@media (max-width: 767.98px) {
				// align-items: center;
			}
			input {
				transition:.1s all ease-in-out;
				width: 19px;
				height: 19px;
				-ms-flex: 0 0 19px;
				flex: 0 0 19px;
				max-width: 19px;
				@media (max-width: 767.98px) {
					width: 12px;
					height: 12px;
					-ms-flex: 0 0 12px;
					flex: 0 0 12px;
					max-width: 12px;
					margin-top: 2px;
				}
				@media (min-width: 767.68px) {
					margin-top: 4px;
				}
			}
		}
		label {
			padding-left: 30px;
			transition:.1s all ease-in-out;
		}
		&.checked {
			.wrapper {
				margin-right: -10px;
				margin-left: -10px;
				padding: 0 10px;
				border-radius: 17px;
				border: 1px solid map-get($color, main-4);
				transition: 0.1s all ease-in-out;
				font-family: Lato-Bold;
			}

			input[type="radio"] {
				&::after {
					position: absolute;
					content: "";
					background: map-get($color, main-4);
					width: 100%;
					height: 100%;
					border-radius: 50%;
				}
			}
			label {
				color: map-get($color, main-4);
				transition: 0.1s all ease-in-out;
			}
		}
		&.active {
			.wrapper {
				margin-right: -10px;
				margin-left: -10px;
				padding: 0 10px;
				border-radius: 17px;
				border: 1px solid map-get($color, main-4);
				transition: 0.1s all ease-in-out;
				font-family: Lato-Bold;
			}
			label {
				color: map-get($color, main-4);
				transition: 0.1s all ease-in-out;
			}
			input[type="radio"] {
				position: relative;
				&::after {
					background: url(../assets/img/check.png) no-repeat !important;
					content: "";
					position: absolute;
					background-size: 100% 100%;
					top: 50%;
					left: 50%;
					width: 100%;
					height: 100%;
					object-fit: cover;
					transform: translate(-50%, -50%);
				}
			}
		}
		&.active-hint {
			label {
				transition: 0.1s all ease-in-out;
				color: map-get($color, main-4);
			}
			input[type="radio"] {
				position: relative;
				&::after {
					transition: 0.1s all ease-in-out;
					content: "";
					position: absolute;
					background: map-get($color, main-4) no-repeat;
					border-radius: 50%;
					background-size: 100% 100%;
					top: 50%;
					left: 50%;
					width: 100%;
					height: 100%;
					transform: translate(-50%, -50%);
				}
			}
		}
		&.disable {
			pointer-events: none;
			.wrapper {
				border: none !important;
			}
			label {
				color: #f60000;
			}
			input[type="radio"] {
				position: relative;
				&::after {
					content: "";
					position: absolute;
					background: url(../assets/img/false.png) no-repeat !important;
					background-size: 100% 100%;
					top: 50%;
					left: 50%;
					width: 100%;
					height: 100%;
					transform: translate(-50%, -50%);
				}
			}
		}
	}
	.answer-btn {
		opacity: 0.5;
		pointer-events: none;
		&.active {
			opacity: 1;
			pointer-events: all;
		}
	}
}
#info-customer {
	display: inline-flex !important;
	align-items: center;
	height: 100%;
	width: 100%;
	overflow-y: auto;
	padding: 100px 0;
	background: url(../assets/img/bg-popup-rule.png) no-repeat;
	background-size: 100% 100%;
	.main-title-img {
		margin-bottom: 10px;
		@media (max-width: 767.98px) {
			margin-bottom: 20px;
		}
	}
	@media (max-width: 1440.98px) {
		overflow-y: auto;
	}
	.sub-title {
		color: map-get($color, main-4);
		font-family: Lato-Bold;
		margin-bottom: 50px;
	}
	.container {
		height: 100%;
		overflow-y: auto;
	}
	.table-info-custom {
		max-width: 820px;
		margin: 0 auto;

		.wrapper {
			margin-bottom: 40px;
			padding: 50px;
			border: 1px solid map-get($color, main-4);
			border-radius: 20px;
			background: rgba($color: #000000, $alpha: 0.16);
			width: 100%;
			@media (max-width: 767.98px) {
				margin-bottom: 20px;
				padding: 20px;
			}
			.item {
				height: 98px;
				width: 100%;
				display: flex;
				flex-wrap: wrap;
				&:not(:last-child) {
					margin-bottom: 20px;
					@media (max-width: 767.98px) {
						margin-bottom: 10px;
					}
				}
				@media (max-width: 767.98px) {
					height: 55px;
				}
				@media (max-width: 575.98px) {
					height: 40px;
				}
				.field {
					-ms-flex: 0 0 32%;
					flex: 0 0 32%;
					max-width: 32%;
					background: linear-gradient(
						270deg,
						#d5b85f 0%,
						#ffffff 100%
					);
					border-radius: 15px 0 0 15px;
					display: flex;
					align-items: center;
					padding-left: 10px;
					font-size: 2.5rem;
					font-family: Lato-Bold;
					color: map-get($color, main-5);
					@media (max-width: 767.98px) {
						font-size: 2rem;
					}
					@media (max-width: 575.98px) {
						font-size: 1rem;
					}
				}
				.field-input,
				.field-select {
					-ms-flex: 0 0 78%;
					flex: 0 0 68%;
					max-width: 68%;
					position: relative;
					input {
						text-align: center;
						&:-internal-autofill-selected {
							color: unset !important;
							background-color: unset !important;
							appearance: none !important;
						}
					}
					input,
					select {
						appearance: none;
						
						border: none;
						color: #ffffff;
						height: 100%;
						width: 100%;
						background: rgba($color: #000000, $alpha: 0.3);
						background-blend-mode: multiply;
						border-radius: 0 15px 15px 0;
						font-size: 2.5rem;
						font-family: Lato-Light;
						opacity: 1;
					}
					select {
						overflow: hidden;
						position: relative;
						padding-right: 55px;
						text-align: right;
						text-align-last: right;
						appearance: none;
						max-width: 100%;
						width: auto;
						border-top-left-radius: 0;
						border-bottom-left-radius: 0;
						@media (max-width: 1024.98px) {
							padding-right: 25px;
						}
						option {
							background: map-get($color, main-1);
							overflow: hidden;
							color: map-get($color, main-8);
						}
					}
				}
				.field-select {
					input {
						display: none;
					}
					&.active {
						input {
						display: block;
						width: 80%;
						position: absolute;
						top: 0;
						left: 0;
						height: 100%;
						border-radius: unset;
					}
					.select-selected  {
						width: 20%;
						margin-left: auto;
					}
					}
				}
			}
		}
		.complete {
			display: flex;
			justify-content: center;
			flex-direction: column;
			.playgame__wrapper {
				&::after {
					content: none;
				}
			}
			.btn-playgame {
				width: 156px;
				height: 60px;
				font-size: 2rem;
				font-family: Lato-Bold;
				&::after,
				&::before {
					content: none;
				}
				@media (max-width: 767.98px) {
					width: 100px;
					height: 40px;
					font-size: 1.6rem;
				}
			}
		}
	}
	button[type="submit"] {
		appearance: none;
		background: none;
		padding: 0;
		border: none;
		justify-content: center;
	}
}

